.app {
  margin: 10px;
  padding: 10px;
  text-align: center;
  border: 1px solid black;
  background-color: #ccc;
}

.form label {
  display: block;
  margin: 20px 0;
  text-align: left;
}

input[type='text'],
textarea {
  width: 100%;
  box-sizing: border-box;
}

textarea {
  height: 150px;
}

button {
  width: 60%;
  padding: 10px 15px;
  text-transform: uppercase;
  cursor: pointer;
}

.result {
  text-align: left;
}

.result p span {
  font-weight: bold;
}

.result button {
  display: block;
  margin: 10px auto;
}

@media(min-width: 1024px) and (orientation: landscape) {
  .app {
    width: 50%;
    max-width: 700px;
    margin: 10px auto;
  }
}